/* big mobile */
@media screen and (min-width: 992px) {
  .header {
    display: flex;
    position: sticky;
    top: 0;
    left: 0;
  }
  .header > section > span {
    display: none;
  }
  .Main-of-site {
    width: calc(100% - 300px);
  }
  .expertise > div > div:last-child > div {
    height: 310px;
    width: 240px;
    margin: 60px 2px;
  }
  .expertise > div > div:last-child > div > aside span {
    top: -16%;
  }
  .expertise > div > div:last-child > div {
    background-color: red !important;
  }
}
