*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
}

$AcolorHover :#2c98f0;
$fontColor :#000000bd;
$purplecolor: #a84cb8;
$yellowcolor: #f9bf3f;
$greencolor: #2fa499;
$blueVioletcolor: #4054b2;
$orangecolor: #ec5453;

.flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

}

body {
    font-family: 'Quicksand';
}

@font-face {
    font-family: 'Quicksand';
    src: url(../fonts/fonts/Quicksand/Quicksand-VariableFont_wght.ttf);
}

@font-face {
    font-family: 'Playfair';
    src: url(../fonts/fonts/Playfair_Display/PlayfairDisplay-Italic-VariableFont_wght.ttf);
    src: url(../fonts/fonts/Playfair_Display/PlayfairDisplay-VariableFont_wght.ttf);
}

@keyframes pictureOfHeader {
    0% {
        border-radius: 50% 50% 53% 47% / 46% 43% 57% 54%;
    }

    // 25%{border-radius: 50% 50% 53% 47% / 46% 43% 57% 54%;}
    50% {
        border-radius: 41% 59% 35% 65% / 60% 38% 62% 40%;
    }

    // 75%{border-radius: 35% 65% 49% 51% / 70% 38% 62% 30% ;}
    100% {
        border-radius: 64% 36% 58% 42% / 60% 38% 62% 40%;
    }
}

.main {
    height: auto;
    display: flex;
    flex-wrap: wrap;
    max-width: 1276px;
    margin: 0 auto;
}

.visible {
    transform: translateX(0) !important;
    transform: translateY(0) !important;
    transform: scale(1) !important;
    opacity: 1 !important;
    visibility: visible !important;

    >* {
        transform: translateX(0) !important;
        transform: translateY(0) !important;
        opacity: 1 !important;
        visibility: visible !important;
    }
}

.header {
    width: 300px;
    height: 100vh;
    position: fixed;
    top: 0;
    left: -300px;
    background-color: #f2f3f7;
    z-index: 999;
    transition: .4s;

    >section {
        width: 100%;
        >span {
            width: 20px;
            height: 20px;
            position: absolute;
            top: 15px;
            right: -50px;
            cursor: pointer;

            >span {
                width: 100%;
                height: 3px;
                border-radius: 3px;
                background-color: black;
                font-size: 1.5rem;
                position: absolute;
                transition: .4s;

                &::before,
                &::after {
                    content: '';
                    width: 100%;
                    height: 3px;
                    background-color: black;
                    border-radius: inherit;
                    position: absolute;
                    left: 0;
                    transition: .8s;
                }

                &::before {
                    top: -5px;
                }

                &::after {
                    top: 5px;
                }
            }
        }

        >.picture {
            height: 35%;

            @extend .flex;

            >figure {
                width: 170px;
                height: 190px;
                margin: 10px 0;
                position: relative;

                &::before {
                    content: '';
                    width: 180px;
                    height: 195px;
                    background-color: rgba(0, 204, 255, 0.233);
                    position: absolute;
                    top: -3px;
                    left: -2.5px;
                    z-index: -1;
                    animation: 4s pictureOfHeader alternate-reverse infinite;
                }

                &::after {
                    content: '';
                    width: 185px;
                    height: 190px;
                    background-color: rgba(255, 0, 0, 0.11);
                    position: absolute;
                    top: -3px;
                    left: -2.5px;
                    z-index: -1;
                    animation: 4s pictureOfHeader alternate infinite;
                }

                >img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    animation: 3s pictureOfHeader alternate infinite;
                }
            }
        }

        >div:last-child {
            height: 65%;
            @extend .flex;

            div:first-child {
                >* {
                    text-align: center;
                }

                >h1 {
                    font-size: 2rem;
                    font-family: 'Playfair';
                }

                >p {
                    font-size: .85rem;
                    font-family: 'Quicksand';
                }
            }

            >div:last-child {
                ul {
                    width: 100%;

                    padding: 0;

                    >li {
                        text-align: center;
                        padding: 7px 0;
                        text-transform: uppercase;


                        >a {
                            color: $fontColor;
                            font-size: 0.8rem;
                            position: relative;

                            &:hover {
                                color: black;
                            }

                            &:hover::before {
                                width: 100%;
                                left: 0;
                            }

                            &::before {
                                content: '';
                                position: absolute;
                                left: 50%;
                                bottom: -5px;
                                width: 0;
                                height: 1px;
                                background-color: $AcolorHover;
                                transition: .4s;
                            }

                        }
                    }
                }

                >p {
                    text-align: center;
                    text-transform: capitalize;
                    font-size: 0.8rem;
                }
            }

        }

    }

}

.titrOfField {
    >div {
        @extend .flex;
        align-items: center;
        justify-content: start;

        div {
            >* {
                color: $fontColor;
                font-size: 16px;
            }
        }

        >div:nth-of-type(1) {
            // transform: translateX(-100px);
            // visibility: hidden;
            // opacity: 0;

            >* {
                text-transform: uppercase;
            }

            >span {
                display: block;
                font-size: 0.7rem;
                letter-spacing: 2px;
                margin-bottom: 20px;
                color: $fontColor;
                font-weight: 600;
            }

            >p {
                font-family: 'Playfair';
                font-weight: bolder;
                font-size: 1.3rem;
                letter-spacing: 4px;
            }
        }

    }
}

.Main-of-site {
    width: 100%;
    // border: 1px solid red;

}

.banner {
    height: 100vh;
    // border: 1px solid green;
    @extend .flex;
    align-items: center;
    background-image: url(../img/banner-of-site-2.jpg);
    background-repeat: no-repeat;
    background-position: right;
    background-size: cover;

    .slider-text {
        width: 100%;

        >* {
            text-transform: capitalize;
        }

