.About {
    >div {
        div {
            padding: 30px 0;
            transition: 2s;
            margin: 10px;

            >* {
                color: $fontColor;
                font-size: 16px;
            }
        }

        >div:nth-of-type(2) {
            transform: translateX(150px);
            visibility: hidden;
            opacity: 0;

            >article {
                >p {
                    font-size: 1rem;
                    line-height: 30px;
                }
            }

        }

        >div:nth-of-type(3) {
            @extend .flex;
            justify-content: space-evenly;

            >div {
                @extend .flex;

                align-items: center;
                position: relative;
                padding: 30px 0;
                box-shadow: 0 0 25px rgba(0, 0, 0, 0.177);
                visibility: hidden;
                opacity: 0;
                transition-delay: .5s;

                >span {
                    width: 100%;
                    text-align: center;
                    display: block;
                    margin-bottom: 20px;
                    font-size: 1.8rem;
                    color: #f9bf40;
                }

                >h4 {
                    font-size: 1rem;
                    margin-bottom: 20px;
                }

                &:before {
                    content: '';
                    width: 100%;
                    height: 2px;
                    background-color: red;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                }
            }

            >div:nth-of-type(1) {
                transform: translate(100px);

                &::before {
                    background-color: $AcolorHover;
                }

                >span {
                    color: $AcolorHover;
                }
            }

            >div:nth-of-type(2) {
                transform: translateY(100px);

                &::before {
                    background-color: #ec5453;
                }

                >span {
                    color: #ec5453;
                }
            }

            >div:nth-of-type(3) {
                transform: translateY(-100px);

                &::before {
                    background-color: #f9bf3f;
                }

                >span {
                    color: #f9bf3f;
                }
            }

            >div:nth-of-type(4) {
                transform: translateX(100px);

                &::before {
                    background-color: #ad56bc;
                }

                >span {
                    color: #ad56bc;
                }
            }
        }
    }
}



