.Education {
    margin: 100px 0;
}

.explainOfEducation {
    @extend .flex;

    >div {
        margin: 5px;
        @extend .flex;
        justify-content: start;
    }

    >div:first-child {

        padding: 25px;
        align-content: center;
        background-color: #f2f3f7;
        transition: .4s;
        cursor: pointer;

        >h4 {
            display: flex;
            justify-content: start;
            align-items: center;
            width: 95%;
            color: inherit;
            font-size: 1rem;

            margin: 0 !important;
        }

        >span {
            width: 5%;

            display: flex;
            justify-content: end;
            align-items: center;
        }
    }

    >div:last-child {
        transition: .4s;
        overflow: hidden;
        padding: 0;
        height: 0;
        border: 1px solid #bdbdbd00;
        color: $fontColor;
    }
}

.Skills {
    >div {
        >div:nth-of-type(2) {
            @extend.flex;

            >div {
                margin: 10px 0;
                padding: 10px 0;

                .progress {
                    width: 90%;
                    position: relative;
                    overflow: visible;

                    >span {
                        display: block;
                        width: 10px;
                        height: 10px;

                        position: absolute;
                        bottom: -2px;
                        border-radius: 50%;

                        >span {
                            color: inherit;
                            font-size: 0.7rem;
                            position: absolute;
                            bottom: 100%;
                            font-weight: bold;
                        }
                    }

                    >div {
                        width: 90%;

                    }

                }

            }

            >div:nth-of-type(1)>div {
                >span {
                    background-color: $AcolorHover;
                    color: $AcolorHover;
                }

                >div {

                    background-color: $AcolorHover;
                }
            }

            >div:nth-of-type(2)>div {
                >span {
                    background-color: $orangecolor;
                    color: $orangecolor;
                }

                >div {
                    background-color: $orangecolor;
                }
            }

            >div:nth-of-type(3)>div {
                >span {
                    background-color: $yellowcolor;
                    color: $yellowcolor;
                }

                >div {
                    background-color: $yellowcolor;
                }
            }

            >div:nth-of-type(4)>div {
                >span {
                    background-color: $purplecolor;
                    color: $purplecolor;
                }

                >div {
                    background-color: $purplecolor;
                }
            }

            >div:nth-of-type(5)>div {
                >span {
                    background-color: $greencolor;
                    color: $greencolor;
                }

                >div {
                    background-color: $greencolor;
                }
            }

            >div:nth-of-type(6)>div {
                >span {
                    background-color: $blueVioletcolor;
                    color: $blueVioletcolor;
                }

                >div {
                    background-color: $blueVioletcolor;
                }
            }


        }
    }
}

