    color: white;
    background-color: #2c98f0 !important;
}

.activeSecondDivOfEducation {
    border: 1px solid #bdbdbd93 !important;
    padding: 25px !important;

}

.Increament {
    padding: 30px 0;
    margin-top: 70px;
    background-image: url(../img/background-skills.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    position: relative;

    &::before {
        content: '';
        width: 100%;
        height: 100%;
        background-color: #0000008a;
        position: absolute;
        top: 0;
        left: 0;


    }

    >div {
        >aside {

            @extend .flex;

            >div {
                z-index: 100;
                margin: 35px 0;

                >span {
                    display: block;
                    width: 100%;
                    text-align: center;
                    text-transform: uppercase;
                    color: white;
                    background-color: transparent;
                    font-family: 'Quicksand';
                    text-shadow: 0 0 3px white;

                }

                >span:nth-of-type(1) {
                    font-size: 1.3rem;
                }

                >span:nth-of-type(2) {
                    font-size: 1.6rem;
                }
            }

        }
    }
}

.mywork {
    margin: 40px 0;

    >div {

        >div:nth-of-type(2) {
            margin: 40px 0;

        }

        >div:nth-of-type(3) {
            @extend .flex;
            justify-content: space-evenly;
            align-items: center;


            >div {
                margin: 20px 0;
                @extend .flex;
                align-items: center;

                height: 300px;
                cursor: pointer;
                position: relative;
                box-shadow: 0 0 5px #0000008a;
                transition: .4s;
                border-radius: 5px;
                overflow: hidden;

                &:hover {
                    transform: scale(1.02);
                    box-shadow: 0 0 10px #0000008a;
                }


                figure {
                    width: 100%;
                    height: 100%;
                    @extend .flex;
                    align-items: center;

                    img {
                        width: 100%;
                        height: 100%;

                    }

                }
            }


        }

        >div:nth-of-type(4) {
            @extend .flex;
            margin: 20px 0;

            >span {
                @extend .flex;

                a {
                    display: flex;
                    padding: 10px 0;
                    justify-content: center;
                    align-items: center;
                    width: 100%;
                    height: 100%;
                    background-color: $AcolorHover;
                    color: white;

                    &:hover {
                        background-color: #2074b9;
                    }

                }
            }
        }
    }

}

.experience {
    margin: 60px 0;

    >div {
        >div:nth-of-type(2) {
            @extend .flex;
            position: relative;
            margin: 20px 0;

            >div {
                background-color: #f2f3f7;
                margin: 20px 0;
                padding: 20px;
                position: relative;

                &::before {
                    content: '';
                    width: 20px;
                    height: 20px;
                    background-color: inherit;
                    position: absolute;
                    top: 5%;
                    left: -10px;
                    transform: rotate(45deg);
                    z-index: -1;
                }

                &::after {
                    content: '';
                    width: 50px;
                    height: 50px;
                    background-color: inherit;
                    position: absolute;
                    top: -5px;
                    left: -65px;

                    border-radius: 50%;

                    z-index: -10;
                }

                >span {
                    font-size: 1.3rem;
                    font-weight: 500;
                    margin-bottom: 20px;
                    font-family: "Quicksand", Arial, sans-serif;
                    text-transform: capitalize;
                    color: black;
                }

                >span:nth-of-type(2) {
                    color: rgba(85, 85, 85, 0.466);
                    display: block;
                    font-weight: lighter;
                    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
                    font-size: 1rem;
                }

                >span:nth-of-type(3) {
                    width: 40px;
                    height: 40px;
                    background-color: inherit;
                    position: absolute;
                    top: 0;
                    left: -60px;
                    transform: rotate(45deg);
                    z-index: -1;
                    border-radius: 50%;
                    transform: rotate(10deg);
                    color: white;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-size: 0.9rem;

                }

                >p {
                    font-size: .95rem;
                }

            }

            >div:nth-of-type(1) {
                span:nth-of-type(3) {
                    background-color: $AcolorHover;
                }
            }

            >div:nth-of-type(2) {
                span:nth-of-type(3) {
                    background-color: $orangecolor;
                }
            }
        }
    }
}

.contact {
    >div {
        >div:nth-of-type(2) {
            @extend .flex;

            >div {

                @extend .flex;

                >div {
                    @extend .flex;
                    align-items: center;
                    justify-content: start;
                    margin: 10px 0;

                    >span {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        width: 80px;
                        height: 80px;
                        background-color: #f2f3f7;
                        margin: 0 10px;

                        >i {
                            font-size: 1.3rem;
                            color: $AcolorHover;
                        }
                    }

                    >p {
                        margin: 0;
                    }
                }
            }
        }
    }
}

.menuVisible {
    left: 0;
    span{
        right: 10px !important;
        top: 15px;
        >span{
            background-color: transparent !important;
            &::Before{
                transform: rotate(45deg) translate(35%);
                
            }
            &::after{
                transform: rotate(-45deg) translate(35%);
            }
        }
    }
}