// tebalet mode
@media screen and (min-width:992px) {
    .header{
        display: flex;
        position: sticky;
        top: 0;
        left: 0;
        >section{
            >span{
                display: none;
            }
        }
    }
    .Main-of-site{
        width: calc(100% - 300px);
    } 
    .expertise{
        >div{
            >div:last-child{
                >div{
                    height: 310px;
                    width: 240px;
                    margin: 60px 2px;
                    >aside{
                        span{
                            top: -16%;
                        }
                    }
                }
                >div{
                    background-color: red !important;
                }
            }
        }
    }
}

